<template>
  <div class="top-content" :key="instanceKey">
    <div class="data-board-box-inner">
      <DataBoardItem
        title="巡检统计(次)"
        icon="inspectionCount"
        :value="data.inspectionCount != null ? data.inspectionCount : '--'"
        :tRatio="data.inspectionYoy != null ? data.inspectionYoy + '%' : '--'"
        :hRatio="data.inspectionQoq != null ? data.inspectionQoq + '%' : '--'"
        percentPosition="right"
        @clickValue="toBiReport('inspection')"
      />
      <DataBoardItem
        title="工单统计(笔)"
        icon="workOrderCount"
        :value="data.workOrderCount != null ? data.workOrderCount : '--'"
        :tRatio="data.workOrderYoy != null ? data.workOrderYoy + '%' : '--'"
        :hRatio="data.workOrderQoq != null ? data.workOrderQoq + '%' : '--'"
        percentPosition="right"
        @clickValue="toBiReport('workOrder')"
      />
      <DataBoardItem
        title="细则统计(万元)"
        icon="netIncome"
        :value="data.netIncome != null ? data.netIncome : '--'"
        :tRatio="data.netIncomeYoy != null ? data.netIncomeYoy + '%' : '--'"
        :hRatio="data.netIncomeQoq != null ? data.netIncomeQoq + '%' : '--'"
        percentPosition="right"
        @clickValue="toBiReport('assess')"
      />
      <DataBoardItem
        title="两票统计(张)"
        icon="twoTicketCount"
        :value="data.twoTicketCount != null ? data.twoTicketCount : '--'"
        :tRatio="data.twoTicketYoy != null ? data.twoTicketYoy + '%' : '--'"
        :hRatio="data.twoTicketQoq != null ? data.twoTicketQoq + '%' : '--'"
        percentPosition="right"
        @clickValue="toBiReport('ticket')"
      />
      <DataBoardItem
        title="缺陷统计(个)"
        icon="defectCount"
        :value="data.defectCount != null ? data.defectCount : '--'"
        :tRatio="data.defectYoy != null ? data.defectYoy + '%' : '--'"
        :hRatio="data.defectQoq != null ? data.defectQoq + '%' : '--'"
        percentPosition="right"
        @clickValue="toBiReport('defect')"
      />
      <DataBoardItem
        title="隐患统计(项)"
        icon="hiddenStatistics"
        :value="data.hiddenCount != null ? data.hiddenCount : '--'"
        :tRatio="data.hiddenYoy != null ? data.hiddenYoy + '%' : '--'"
        :hRatio="data.hiddenQoq != null ? data.hiddenQoq + '%' : '--'"
        percentPosition="right"
        @clickValue="toBiReport('hidden')"
      />
    </div>
  </div>
</template>

<script lang="ts" setup>
  import { ref, defineProps } from 'vue';
  import DataBoardItem from '/@/components/Framework/Chart/DataBoardItem.vue';
  import { handleRouteChange } from '../../utils';
  import { buildUUID } from '@/utils/uuid';
  // import * as OperationApi from '@/api/da/cockpit/operation';

  const props = defineProps({
    data: { type: Object, default: {} },
  });
  const instanceKey = ref(buildUUID());

  // ==================================数据穿透 ======================================
  //指标数据
  const toBiReport = (type: string) => {
    if (type == 'inspection') {
      handleRouteChange('/bi/inspection/index', {});
    } else if (type == 'workOrder') {
      handleRouteChange('/bi/maintenance/workorder', {});
    } else if (type == 'ticket') {
      handleRouteChange('/bi/maintenance/ticket', {});
    } else if (type == 'defect') {
      handleRouteChange('/bi/fault/index', {});
    } else if (type == 'hidden') {
      handleRouteChange('/bi/hidden/index', {});
    } else if (type === 'assess') {
      handleRouteChange('/bi/maintenance/assess', {});
    }
  };

  // 更新组件函数
  const handleUpdate = () => {
    instanceKey.value = buildUUID();
  };

  defineExpose({
    handleUpdate,
  });
</script>

<style lang="less" scoped>
  .top-content {
    flex: 11.32%;
    height: 11.32%;

    .data-board-box-inner {
      display: flex;
      justify-content: space-between;
      height: 100%;

      :deep(.data-board-item) {
        width: 15.73vw;
        height: 100%;
        padding-left: 0.2rem;

        .title-box {
          .icon-box {
            margin-right: 0.16rem;
          }

          .title-text {
